import __var_0__ from '@stdlib/math/base/assert/is-nan';
import __var_1__ from '@stdlib/math/base/special/digamma';
import __var_2__ from '@stdlib/math/base/special/gammaln';
import __var_3__ from '@stdlib/constants/math/float64-ln-two';
